import React, {useState} from 'react';
import './form.css';
import Checkmark from "./checkmark";
import Ari from "./foto all MT/Ari Ratna Gumilang.jpeg";
import Astrid from "./foto all MT/Astrid.jpeg";
import Dita from "./foto all MT/Dita Khairunnisa.jpeg";
import Edi from "./foto all MT/Edi Kurniadi.jpeg";
import Elisa from "./foto all MT/Elisa.jpeg";
import Fahmi from "./foto all MT/Fahmi Murtadha.jpeg";
import Kiki from "./foto all MT/Felicia Kiki Simanjaya.jpeg";
import Novi from "./foto all MT/Felicia Novi Simanjaya.jpeg";
import Inda from "./foto all MT/Inda Purnama.jpeg";
import Indah from "./foto all MT/Indah Eka Asriani.jpeg";
import Sawir from "./foto all MT/Musawwir Usman.jpeg";
import Nadia from "./foto all MT/Nadia Andini.jpeg";
import Nunung from "./foto all MT/Nunung Kadarsih.jpeg";
import Nury from "./foto all MT/Nury Kamelia.jpeg";
import Oki from "./foto all MT/Oki Anggara.jpeg";
import Rifqah from "./foto all MT/Rifqah Sajidah.jpeg";
import Sari from "./foto all MT/Sari Setiawati.jpeg";
import Shafira from "./foto all MT/Shafira Kurnia.jpeg";

const nominatorsImg = [Ari, Astrid, Dita, Edi, Elisa, Fahmi, Kiki, Novi, Inda, Indah, Sawir, Nadia, Nunung, Nury, Oki, Rifqah, Sari, Shafira];
const nominatorsName = ['Ari', 'Astrid', 'Dita', 'Edi', 'Elisa', 'Fahmi', 'Kiki', 'Novi', 'Inda', 'Indah', 'Sawir', 'Nadia', 'Nunung', 'Nury', 'Oki', 'Rifqah', 'Sari', 'Shafira'];

const Form = React.memo(({handleClick, fav1, fav2, fav3}) => {

	const [state, setState] = useState('form__entering');
	const [showError, setShowError] = useState(false);
	const [favorit1, setFavorit1] = useState(fav1);
	const [favorit2, setFavorit2] = useState(fav2);
	const [favorit3, setFavorit3] = useState(fav3);

	const checkNominator = (me, favorit1, favorit2, favorit3) => {
		if(favorit1 && favorit2 && favorit3) {
			if(me === favorit1 || me === favorit2 || me === favorit3) return true;
			return false;
		}
		return true;
	}

	setTimeout(() => {
		if( state === 'form__entering') setState('form');
	}, 200);

	const buttonClicked = (e, nominator) => {
		setShowError(false);
		let component = e.target;
		if(component.nodeName === "IMG" || component.nodeName === 'DIV') {
			component = component.parentNode;
		}
		if(component.nodeName === "svg") {
			component = component.parentNode.parentNode;
		}

		component.classList.add('nominator-button-clicked');
		setTimeout(() => {
			component.classList.remove('nominator-button-clicked');
		}, 100);
		setTimeout(() => {
			component.classList.add('nominator-button-clicked');
		}, 200);
		setTimeout(() => {
			component.classList.remove('nominator-button-clicked');
			if(!favorit1 && favorit2 !== nominator && favorit3 !== nominator) {setFavorit1(nominator) ; return}
			if(favorit1 === nominator) {setFavorit1(null) ; return}
			if(!favorit2 && favorit3 !== nominator) {setFavorit2(nominator) ; return}
			if(favorit2 === nominator) {setFavorit2(null) ; return}
			if(!favorit3) {setFavorit3(nominator) ; return}
			if(favorit3 === nominator) {setFavorit3(null) ; return}
		}, 300);
	}

	const handleSubmit = (e) => {
		e.target.classList.add('submit-button-clicked');
		setTimeout(() => {
			e.target.classList.remove('submit-button-clicked');
		}, 100);
		setTimeout(() => {
			e.target.classList.add('submit-button-clicked');
		}, 200);
		setTimeout(() => {
			e.target.classList.remove('submit-button-clicked');
			if(favorit1 && favorit2 && favorit3) {
				setState('form__exiting');
			} else {
				setShowError(true);
			}
		}, 300);
		setTimeout(() => {
			if(favorit1 && favorit2 && favorit3) {
				handleClick(favorit1, favorit2, favorit3);
			}
		},350)
	}

	  return (
		   <div className={`${state}`}>
			   	<h3 className="form__title">Pilih 3 orang Master Teacher favorit kamu selama di Brain Academy?</h3>
			   	<h4 className="form__description">jawabanmu bersifat anonim</h4>
		   	<div className="nominators__wrapper">
		   		{nominatorsImg.map( (nominator, index) => {
		   			return <button 
		   						key={nominatorsName[index]} 
		   						className={`nominator__button ${checkNominator(nominatorsName[index], favorit1, favorit2, favorit3) ? null : 'nominator__button-dark'}`} 
		   						onClick={(e) => buttonClicked(e, nominatorsName[index])} 
		   						>
			   						<img className={`nominator__img ${checkNominator(nominatorsName[index], favorit1, favorit2, favorit3) ? null : 'nominator__img-dark'}`}  src={nominator} alt=""/>
			   						<div className={`nominator__text ${checkNominator(nominatorsName[index], favorit1, favorit2, favorit3) ? null : 'nominator__text-dark'}`} >{`kak ${nominatorsName[index]}`}</div>
			   						{favorit1 === nominatorsName[index] ? <Checkmark/> : null}
			   						{favorit2 === nominatorsName[index] ? <Checkmark/> : null}
			   						{favorit3 === nominatorsName[index] ? <Checkmark/> : null}
		   					</button>
		   		})}
		   	</div>
		   	<div>
		   		{showError 
		   			? <div className='error-text-wrapper'>
		   				<p className='error-text'>Harus pilih 3 Master Teacher</p>
		   			  </div> 
		   			: null
		   		}
		   		
		   		<button className='form__submit-button' onClick={handleSubmit}>Submit</button>
		   	</div>
		   </div>
	  )
});

export default Form;